* {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
        Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    max-width: 100vw;
}
body {
    height: 100vh;
    margin: 0;
    max-width: 100vw;
    overflow-x: hidden;
}
.h-full {
    height: 100%;
}
.Cover {
    position: relative;
    height: inherit;
    max-height: 100vh;
    max-width: 100%;
}
.Cover::before {
    content: "";
    background: inherit;
    position: absolute;
    top: 0;
    left: 0;
    filter: blur(5px);
    height: 100%;
    width: 100%;
}

.background-img {
    position: absolute;
    max-width: 100%;
}
.background-txt {
    width: 100%;
    position: absolute;
}
.row1 {
    font-size: 3.5rem;
    width: 100%;
}
.welcomeImg {
    background: url("./Assests/Images/smartphone.jpg");
    min-height: 100%;
    max-width: 100%;
    background-size: cover;
    background-position: center;
}
.welcomeImg1 {
    background-image: url("./Assests/Images/screenReplacement.png");
    min-height: 100%;
    max-width: 100%;
    background-size: 100% 100%;
}
.Services:hover{
  cursor: pointer;
  box-shadow: 0px 0px  10px #000;
}
