.HomeCover {
    background-image: url("./Assests/Images/Home.jpg");
    background-size: 100% 100%;
    height: 90vh;
}
.HomeCover::before {
    filter: blur(3px);
}
.ContactCover {
    background-image: url("./Assests/Images/contact.jpg");
    background-size: 100% 100%;
    height: 100%;
    min-width: 100%;
}
.AboutCover {
    background-image: url("./Assests/Images/welcome.jpg");
    background-size: 100% 100%;
    height: 100%;
    min-width: 100%;
}
.AboutCover::before {
    filter: blur(0);
}
.valueList li::marker {
    color: green;
}
.smartPhoneCover {
    background-image: url("./Assests/Images/test1.jpg");
    background-position: right;
    min-height: 80vh;
}

.TabletCover {
    background: url("./Assests/Images/iphone.jpg") ;
    background-size: cover;
    height: 80vh;
}
.ComputerCover {
    background: url("./Assests/Images/computer2.jpg");
    background-size: cover;
}
.LaptopCover {
    background: url("./Assests/Images/laptop2.jpg");
    background-position-y: bottom;
    background-position-x: right;
}
.ServiceCover {
    background: url("./Assests/Images/computer2.jpg");
    background-size: cover;
}
/* Medium devices (mobiles, 768px and below) */

@media screen and (max-width: 767.98px) {
    .welcomeTxt{
        font-size: 3rem;
    }
    .Cover {
        height: 60vh;
    }
    .welcomeImg {
        padding: 10rem;
    }
    .welcomeImg1 {
        padding: 10rem;
    }
}

/* Medium devices (landscape tablets, 768px and up) */
@media (min-width: 768px) and (max-width: 991.98px) {
    .row1 {
        font-size: 3rem;
    }
    img {
        max-height: 25rem;
    }
    .HomeCover {
        height: 30rem;
    }
    .smartPhoneCover {
        height: 25rem;
    }
    .AboutCover {
        height: 25rem;
    }
    .ContactCover {
        height: 25rem;
    }
}
